import React, { useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Gallery, Group, Header, CardScroll, Card, ScreenSpinner } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import CustomCard from '../components/CustomCard'
import CustomGallery from '../components/CustomGallery'

import { getProgress } from '../utils/ProgressParser'

import axios from 'axios'

const osName = platform();



const Progress = ({ setPopout, go, id }) => {
	const [ subjects, setSubjects ] = useState(null)
	const [ error, setError ] = useState(null)

	const scheme = document.body.attributes.getNamedItem("scheme").value

	useEffect(() => {
		setPopout(<ScreenSpinner size='large' />)
		
		axios.get('https://tsu-helper-server.herokuapp.com/getProgress?num=181434')
		.then(json => {
			if(!json.data.err){
				setSubjects(json.data.doc.data)
			}
		})
		.then(() => setPopout(null))
		.catch((e) => {
			setError(e)
			setPopout(null)
		})
	},[])
	return (
	<Panel id={id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Зачётка
		</PanelHeader>
		{ subjects && subjects.map((item, term) => {
			return (<div className={scheme == "space_gray" ? "card-block dark" : "card-block"}>
				<CardScroll size="s">
					{item.map(i => <Card onClick={go} data-to="persik"><CustomCard data={i}/></Card>)}
				</CardScroll>
				<div class="card-divider">Семестр {term+1}</div>
			</div>)
		})}
		{/* {subjects && <Group description="Семестр 1">
      <CardScroll size="s">
		{subjects.map(item => (<Card onClick={go} data-to="persik">
          <CustomCard data={item}/>
        </Card>))}
        
      </CardScroll>
    </Group> } */}
		{/* <Group header={<Header mode="secondary">Семестр 1</Header>}>
              <Gallery
                slideWidth="90%"
                style={{ height: 150 }}
                bullets="dark"
				align="center"
              >
                {subjects.map(item => (<CustomGallery data={item}/>))}
              </Gallery>
            </Group> */}
			{error && <div>{`Выявлена ошибка: ${JSON.stringify(error)}`}</div>}
	</Panel>
)
		};

Progress.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Progress;
